@font-face {
  font-family: 'icomoon';
  src:  url('fonts/icomoon.eot?q2s139');
  src:  url('fonts/icomoon.eot?q2s139#iefix') format('embedded-opentype'),
    url('fonts/icomoon.ttf?q2s139') format('truetype'),
    url('fonts/icomoon.woff?q2s139') format('woff'),
    url('fonts/icomoon.svg?q2s139#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-question:before {
  content: "\f128";
}
.icon-search:before {
  content: "\f002";
}
.icon-crosshairs:before {
  content: "\f05b";
}
.icon-venus:before {
  content: "\f221";
}
.icon-mars:before {
  content: "\f222";
}
